*{
    padding:0;
    margin:0;
}
ul,li{
    list-style:none;
}
a{
    text-decoration:none;
}
body{
    background-color:#ddd;
}
/* header */
.headerwrap{
    height:50px;
    background-color:#444;
}
.headerwrap header{
    width:1200px;
    margin:0 auto;
}
.headerwrap img{
    margin-top:10px;
    width:120px;
}
.headerwrap input{
    margin-left:20px;
    padding-top:15px;
    width:200px;
    height:24px;
    background:url("https://static2.cnodejs.org/public/images/search.e53b380a.hashed.png") 5px 3px no-repeat;
    background-color:#aaa;
    opacity:0.6;
    border:none;
    border-radius:12px;
}
.headerwrap input:hover{
    background-color:#fff;
    opacity:0.9;
}
.headerwrap a{
    font-size:12px;
    margin:0 20px;
    color:#fff;
}
/* main */
.main{
    width:1200px;
    margin:30px auto;
}
.content{
    width:900px;
    float:left;
    background:#fff;
}
.home{
    height:40px;
    background-color:#eee;
}
.home a{
    color:rgb(134, 192, 58);
    font-size:14px;
    padding:5px;
    margin:10px;
    line-height:40px;
}
.home a:hover{
    color:rgb(4, 22, 185);
}
.home a:focus{
    color:#fff;
    background:rgb(134, 192, 58);
    border-radius:4px;
}
/*all/good/job/ask/share*/
.li{
    width:880px;
    height:30px;
    font-size:14px;
    padding:10px;
    border-bottom:1px solid #eee;
}
.li:hover{
    background-color:#eee;
    text-decoration:underline;
}
.li img{
    height:30px;
    border-radius:3px;
    float: left;
}
.li span{
    height:30px;
    line-height:30px;
    color:#888;
    font-size:13px;
}
.li .count{
    width:80px;
    float: left;
    text-align: center;
}
.li .count .visit{
    font-size:12px;
    color:#aaa;
}
.li .title{
    color:#000;
    font-size:15px;
    height:30px;
    line-height:30px;
}
.li .right{
    float:right;
}
.li .right img{
    height:20px;
    line-height:30px;
}
/*pages*/
.pages ul{
    margin:10px;
    width:491px;
    height:30px;
    border:1px solid #aaa;
    border-radius:5px;
}
.pages a,.pages li{
    width:40px;
    height:30px;
    text-align:center;
    line-height:30px;
    float:left;
    border-right:1px solid #aaa;
}
.pages a:focus {
    background-color:rgb(134, 192, 58);
}
.pages li:hover{
    background-color:#ccc;
}
.pages li:last-child{
    border-right:none;
}
/*login*/
.login{
    width:700px;
    margin:100px;
}
.login p{
    width:100%;
    height:50px;
}
.login div{
    width:50px;
    height:30px;
    line-height:30px;
    padding:10px;
    float:left;
    text-align:right;
}
.login input{
    width:280px;
    height:28px;
    margin:10px;
    border:none;
    border:1px solid #bbb;
    border-radius:4px;
    float:left;
}
.login .loginBtn{
    margin:10px 70px;
}
.login button,.sider button{
    padding:10px;
    margin:10px;
    color:#fff;
    background-color:rgb(71, 188, 243);
    border:none;
    border-radius:4px;
}
/*sider*/
.sider{
    width:270px;
    float:right;
    background-color:#fff;
}
.sider p{
    font-size:14px;
    padding:10px;
}
.sider p a{
    color:#444;
    padding:0 5px;
}
.sider img {
    width:250px;
    height:60px;
    padding:5px 10px;
}